import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Message from '../components/Message';
import Loader from './../components/Loader';
import { Form, Button, Container, Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { listProductDetails, updateProduct } from './../actions/productActions';
import { PRODUCT_UPDATE_RESET } from './../constants/productConstants';
import axios from 'axios';

const ProductEditScreen = ({ match, history }) => {
    const productId = match.params.id;
    const [name, setName] = useState('');
    const [price, setPrice] = useState(0);
    const [image, setImage] = useState('');
    const [brand, setBrand] = useState('');
    const [category, setCategory] = useState('');
    const [countInStock, setCountInStock] = useState(0);
    const [description, setDescription] = useState('');
    const [uploading, setUploading] = useState(false);

    const dispatch = useDispatch();
    const productDetails = useSelector(state => state.productDetails);

    const { loading,product, error } = productDetails;

    const productUpdate = useSelector(state => state.productUpdate);

    const { loading:loadingUpdate,success:successUpdate, error:errorUpdate } = productUpdate;

    const submitHandler = e => {
        e.preventDefault();
        dispatch(updateProduct({
            _id:productId,
            name,
            price,
            image,
            brand,
            category,
            description,
            countInStock
        }));
    };

    const uploadFileHandler =async(e)=>{
        const file = e.target.files[0];
        const formData = new FormData();
        formData.append('image', file)
        setUploading(true);

        try {
            const config={
                headers:{
                    'Content-Type':'multipart/form-data'
                }
            }
            const {data}= await axios.post('/api/upload',formData,config);

            setImage(data);
            setUploading(false);

        } catch (error) {
            console.error(error);
            setUploading(false);
        }

    }

    useEffect(() => {

        if(successUpdate){
            dispatch({ type: PRODUCT_UPDATE_RESET });
            history.push('/admin/productlist');
        }else{

            if(!product || product._id !== productId){
                dispatch(listProductDetails(productId));
            }else{
                setName(product.name);
                setPrice(product.price);
                setImage(product.image);
                setBrand(product.brand);
                setCategory(product.category);
                setCountInStock(product.countInStock);
                setDescription(product.description);
            }
        }

        
    }, [product,dispatch,productId,history,successUpdate]);

    return <>
    <Link to='/admin/productlist' className="btn btn-light my-3">Go Back</Link>
    <Container>
        <Row className='justify-content-md-center'>
            <Col xs={12} md={6}>
                <h1>Edit Product</h1>
                {loadingUpdate && <Loader/>}
                {errorUpdate && <Message varient='danger' >{errorUpdate}</Message>}
                {loading ? <Loader /> : error? <Message varient='danger'>{error}</Message>:(
                <Form onSubmit={submitHandler}>
                    <Form.Group controlId='name'>
                        <Form.Label>Name</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="Enter Name" value={name} onChange={e => setName(e.target.value)}
                        ></Form.Control>
                    </Form.Group>

                    <Form.Group controlId='price'>
                        <Form.Label>Price</Form.Label>
                        <Form.Control
                            type="number"
                            placeholder="Enter Price" value={price} onChange={e => setPrice(e.target.value)}
                        ></Form.Control>
                    </Form.Group>

                    <Form.Group controlId='image'>
                        <Form.Label>Image</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="Enter Image URL" value={image} onChange={e => setImage(e.target.value)}
                        ></Form.Control>
                        <Form.File id='image-file' label='Choose File' custom onChange={uploadFileHandler}></Form.File>
                        {uploading && <Loader />}
                    </Form.Group>

                    <Form.Group controlId='brand'>
                        <Form.Label>Brand</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="Enter Brand" value={brand} onChange={e => setBrand(e.target.value)}
                        ></Form.Control>
                    </Form.Group>

                    <Form.Group controlId='countInStock'>
                        <Form.Label>Count In Stock</Form.Label>
                        <Form.Control
                            type="number"
                            placeholder="Enter Count In Stock" value={countInStock} onChange={e => setCountInStock(e.target.value)}
                        ></Form.Control>
                    </Form.Group>

                    <Form.Group controlId='category'>
                        <Form.Label>Category</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="Enter Category" value={category} onChange={e => setCategory(e.target.value)}
                        ></Form.Control>
                    </Form.Group>

                    <Form.Group controlId='description'>
                        <Form.Label>Description</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="Enter Description" value={description} onChange={e => setDescription(e.target.value)}
                        ></Form.Control>
                    </Form.Group>

                    <Button type='submit' onClick={submitHandler} variant='primary'>Update</Button>

                </Form>
                )}
            </Col>
        </Row>
    </Container>
    </>
}

export default ProductEditScreen
