import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Message from '../components/Message';
import Loader from './../components/Loader';
import { Form, Button, Container, Row, Col } from 'react-bootstrap';
import { getUserDetails, updateUser } from './../actions/userActions';
import { Link } from 'react-router-dom';
import { USER_UPDATE_RESET } from './../constants/userConstants';

const UserEditScreen = ({ match, history }) => {
    const userId = match.params.id;
    const [email, setEmail] = useState('');
    const [isAdmin, setIsAdmin] = useState(false);
    const [name, setName] = useState('');


    const dispatch = useDispatch();
    const userDetails = useSelector(state => state.userDetails);

    const { loading,user, error } = userDetails;

    const userUpdate = useSelector(state => state.userUpdate);

    const { loading:loadingUpdate,success:successUpdate, error:errorUpdate } = userUpdate;

    const submitHandler = e => {
        e.preventDefault();

        //Dispatch register
        dispatch(updateUser({_id:userId, name, email,isAdmin}))
    };


    useEffect(() => {
        if(successUpdate){
            dispatch({ type:USER_UPDATE_RESET });
            history.push('/admin/userlist');
        }else{
            if(!user || user._id !== userId){
                dispatch(getUserDetails(userId));
            }else{
                setName(user.name);
                setEmail(user.email);
                setIsAdmin(user.isAdmin === 'true'? true:false);
            }
        }
        
    }, [user,dispatch,userId,history,successUpdate]);

    return <>
    <Link to='/admin/userlist' className="btn btn-light my-3">Go Back</Link>
    <Container>
        <Row className='justify-content-md-center'>
            <Col xs={12} md={6}>
                <h1>Edit User</h1>
                {loadingUpdate && <Loader />}
                {errorUpdate && <Message varient='danger'>{errorUpdate}</Message>}
                {loading ? <Loader /> : error? <Message varient='danger'>{error}</Message>:(
                <Form onSubmit={submitHandler}>
                    <Form.Group controlId='name'>
                        <Form.Label>Name</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="Enter Name" value={name} onChange={e => setName(e.target.value)}
                        ></Form.Control>
                    </Form.Group>

                    <Form.Group controlId='email'>
                        <Form.Label>Email Address</Form.Label>
                        <Form.Control
                            type="email"
                            placeholder="Enter Email" value={email} onChange={e => setEmail(e.target.value)}
                        ></Form.Control>
                    </Form.Group>

                    <Form.Group controlId='isAdmin'>
                        <Form.Check
                            type="checkbox"
                            label="Is Admin" 
                            value={isAdmin}
                            checked = {isAdmin}
                            onChange={e => setIsAdmin(e.target.checked)}
                        ></Form.Check>
                    </Form.Group>

                    <Button type='submit' variant='primary'>Update</Button>

                </Form>
                )}
            </Col>
        </Row>
    </Container>
    </>
}

export default UserEditScreen
