import React,{useEffect} from 'react'
import { Button, Row, Col, ListGroup, Image, Card } from 'react-bootstrap';
import CheckOutSteps from '../components/CheckOutSteps';
import Message from './../components/Message';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { createOrder } from './../actions/orderAction';


const PlaceOrderScreen = ({ history }) => {

    const dispatch = useDispatch()
    const cart = useSelector(state => state.cart);

    const addDecimals = (num) =>{
        return (Math.round(num * 100)/100).toFixed(2)
    }

    //calculate price
    cart.itemPrice = addDecimals(cart.cartItems.reduce((acc,item)=> acc+item.price * item.qty, 0));

    //calculate shipping price
    cart.shippingPrice = addDecimals(cart.itemPrice > 100 ? 0 : 100);

    //calculate tax price
    cart.taxPrice = addDecimals((0.15 * cart.itemPrice).toFixed(2));

    //calculate total price
    cart.totalPrice = (Number(cart.itemPrice) + Number(cart.shippingPrice) + Number(cart.taxPrice)).toFixed(2)
    
    const orderCreate = useSelector(state=> state.orderCreate);

    const { order, error, success } = orderCreate;

    useEffect(()=>{
        if(success){
            history.push(`/order/${order._id}`);
        }
        // eslint-disable-next-line
    },[history,success])

    const placeOrderHandle = () =>{
        dispatch(createOrder({
            orderItems: cart.cartItems,
            shippingAddress: cart.shippingAddress,
            paymentMethod: cart.paymentMethod,
            itemsPrice: cart.itemPrice,
            shippingPirce: cart.shippingPirce,
            taxPrice: cart.taxPrice,
            totalPrice: cart.totalPrice
        }))
    }

    return <>
        <CheckOutSteps step1 step2 step3 step4 />
        <Row>
            <Col md={8}>
                <ListGroup variant='flush'>
                    <ListGroup.Item>
                        <h2>Shipping</h2>
                        <p>
                            <strong>Address:</strong>
                            {cart.shippingAddress.address},{cart.shippingAddress.city},{cart.shippingAddress.postalCode},{cart.shippingAddress.country}
                        </p>
                    </ListGroup.Item>

                    <ListGroup.Item>
                        <h2>Payment Method</h2>
                        <strong>Method: </strong>
                        {cart.paymentMethod}
                    </ListGroup.Item>
                    
                    <ListGroup.Item>
                        <h2>Order Items</h2>
                        {cart.cartItems.length === 0? <Message>Your cart is empty</Message> : (
                            <ListGroup variant='flush'>
                                {cart.cartItems.map((item,index)=>(
                                    <ListGroup.Item key={index}>
                                        <Row>
                                            <Col md={1} >
                                                <Image src={item.image} alt={item.name} fluid rounded />
                                            </Col>
                                            <Col>
                                                <Link to={`/product/${item.product}`}>{item.name}</Link>
                                            </Col>
                                            <Col md={4}>
                                                {item.qty} x ${item.price} = ${item.qty * item.price}
                                            </Col>
                                        </Row>
                                    </ListGroup.Item>
                                ))}
                            </ListGroup>
                        )}
                    </ListGroup.Item>

                </ListGroup>
            </Col>

            <Col md={4}>
                <Card>
                    <ListGroup variant='flush'>
                        <ListGroup.Item>
                            <h2>Order Summery</h2>
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Row>
                                <Col>Items</Col>
                                <Col>${cart.itemPrice}</Col>
                            </Row>
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <Row>
                                <Col>Shipping</Col>
                                <Col>${cart.shippingPrice}</Col>
                            </Row>
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <Row>
                                <Col>Tax</Col>
                                <Col>${cart.taxPrice}</Col>
                            </Row>
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <Row>
                                <Col>Total</Col>
                                <Col>${cart.totalPrice}</Col>
                            </Row>
                        </ListGroup.Item>
                        
                        <ListGroup.Item>
                            {error && <Message varient='danger'>{error}</Message>}
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <Button type='button' className='btn-block' disabled={cart.cartItems === 0} onClick={placeOrderHandle}>Place Order</Button>
                        </ListGroup.Item>
                    </ListGroup>
                </Card>
            </Col>
        </Row>
    </>
}

export default PlaceOrderScreen
