//jshint esversion:10

import {
    PRODUCT_LIST_FAIL, 
    PRODUCT_LIST_REQUEST, 
    PRODUCT_LIST_SUCCESS, 
    PRODUCT_DETAILS_FAIL, 
    PRODUCT_DETAILS_REQUEST, 
    PRODUCT_DETAILS_SUCCESS, 
    PRODUCT_DELETE_SUCCESS,
    PRODUCT_CREATE_REQUEST,
    PRODUCT_CREATE_SUCCESS,
    PRODUCT_CREATE_FAIL,
    PRODUCT_UPDATE_REQUEST,
    PRODUCT_UPDATE_SUCCESS,
    PRODUCT_DELETE_REQUEST,PRODUCT_DELETE_FAIL, PRODUCT_UPDATE_FAIL, PRODUCT_CREATE_REVIEW_REQUEST, PRODUCT_CREATE_REVIEW_SUCCESS, PRODUCT_CREATE_REVIEW_FAIL
} from '../constants/productConstants';
import axios from 'axios';

export const listProducts = (keyword="")=> async(dispatch) => {
    try {
        dispatch({ type: PRODUCT_LIST_REQUEST });
        console.log(keyword);

        const { data } = await axios.get(`/api/products?keyword=${keyword}`);

        dispatch({
            type: PRODUCT_LIST_SUCCESS,
            payload: data
        });

    } catch (error) {
        dispatch({
            type: PRODUCT_LIST_FAIL,
            payload: error.response && error.response.data.msg ?
            error.response.data.msg : error.message
        });
    }
};

export const listProductDetails = (id)=> async(dispatch) => {
    try {
        dispatch({ type: PRODUCT_DETAILS_REQUEST });

        const { data } = await axios.get(`/api/products/${id}`);
        dispatch({
            type: PRODUCT_DETAILS_SUCCESS,
            payload: data
        });

    } catch (error) {
        dispatch({
            type: PRODUCT_DETAILS_FAIL,
            payload: error.response && error.response.data.msg ?
            error.response.data.msg : error.message
        });
    }
};

export const productDelete = (id) => async (dispatch, getState)=>{
    try {

        const { userLogin: { userInfo } } = getState();

        dispatch({
            type: PRODUCT_DELETE_REQUEST
        })

        const config = {
            headers:{
                "Content-Type":"application/json",
                Authorization: `Bearer ${userInfo.token}`
            }
        }

         const { data } = await axios.delete(`/api/products/${id}`,config)

         dispatch({
             type: PRODUCT_DELETE_SUCCESS,
             payload: data
         });

    } catch (error) {
        dispatch({
            type: PRODUCT_DELETE_FAIL,
            payload: error.response && error.response.data.msg ?
            error.response.data.msg : error.message
        });
    }
}

export const createProduct = () => async (dispatch, getState)=>{
    try {

        const { userLogin: { userInfo } } = getState();

        dispatch({
            type: PRODUCT_CREATE_REQUEST
        })

        const config = {
            headers:{
                "Content-Type":"application/json",
                Authorization: `Bearer ${userInfo.token}`
            }
        }

         const { data } = await axios.post(`/api/products/`,{},config)

         dispatch({
             type: PRODUCT_CREATE_SUCCESS,
             payload: data
         });

    } catch (error) {
        dispatch({
            type: PRODUCT_CREATE_FAIL,
            payload: error.response && error.response.data.msg ?
            error.response.data.msg : error.message
        });
    } 
}

export const updateProduct = (product) => async (dispatch, getState)=>{
    try {

        const { userLogin: { userInfo } } = getState();

        dispatch({
            type: PRODUCT_UPDATE_REQUEST
        })

        const config = {
            headers:{
                "Content-Type":"application/json",
                Authorization: `Bearer ${userInfo.token}`
            }
        }

         const { data } = await axios.put(`/api/products/${product._id}`,product,config)
         dispatch({
             type: PRODUCT_UPDATE_SUCCESS,
             payload: data
         });

    } catch (error) {
        dispatch({
            type: PRODUCT_UPDATE_FAIL,
            payload: error.response && error.response.data.msg ?
            error.response.data.msg : error.message
        });
    }
}

export const createProductReview = (productId,review) => async (dispatch, getState)=>{
    try {

        const { userLogin: { userInfo } } = getState();

        dispatch({
            type: PRODUCT_CREATE_REVIEW_REQUEST
        })

        const config = {
            headers:{
                "Content-Type":"application/json",
                Authorization: `Bearer ${userInfo.token}`
            }
        }

         const {data} = await axios.post(`/api/products/${productId}/reviews`,config)
         dispatch({
             type: PRODUCT_CREATE_REVIEW_SUCCESS,
             payload: data
         });

    } catch (error) {
        dispatch({
            type: PRODUCT_CREATE_REVIEW_FAIL,
            payload: error.response && error.response.data.msg ?
            error.response.data.msg : error.message
        });
    }
}