import React, { useEffect } from 'react'
import { Row, Col, ListGroup, Image, Card, Button } from 'react-bootstrap';
import Message from './../components/Message';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { deliverOrder, getOrderDetails } from './../actions/orderAction';
import Loader from './../components/Loader';
import { ORDER_DELIVER_RESET } from './../constants/orderConstants';

const OrderScreen = ({ match }) => {

    const orderId = match.params.id

    const dispatch = useDispatch();

    const orderDetails = useSelector(state => state.orderDetails);
    const { order, error, loading } = orderDetails;

    const userLogin = useSelector(state => state.userLogin);
    const { userInfo } = userLogin;

    const orderDeliver = useSelector(state => state.orderDeliver);
    const { success:successDeliver, loading:loadingDeliver, error:errorDeliver } = orderDeliver;

    useEffect(() => {
        dispatch(getOrderDetails(orderId))

        if(successDeliver){
            dispatch({ type: ORDER_DELIVER_RESET })
        }

    }, [orderId, dispatch, successDeliver])

    const deliverHandler =()=>{
        dispatch(deliverOrder(order));
    }

    return loading ? <Loader /> : error ? <Message varient='danger'>{error}</Message> : <>
        <h1>Order {order._id}</h1>
        <Row>
            <Col md={8}>
                <ListGroup variant='flush'>
                    <ListGroup.Item>
                        <h2>Shipping</h2>
                        <strong style={{fontWeight:'bolder'}}>Name:</strong>{order.user.name} <br/>
                        <strong style={{ fontWeight: 'bolder' }}>Email:</strong><a href={`mailto:${order.user.email}`}>{order.user.email}</a>
                        
                        <p>
                            <strong>Address:</strong>
                            {order.shippingAddress.address},{order.shippingAddress.city},{order.shippingAddress.postalCode},{order.shippingAddress.country}
                        </p>
                        {order.isDelivered ? <Message varient='success'>Delivered on {order.deliveredAt}</Message> : <Message varient='danger'>Not Delivered</Message>}
                    </ListGroup.Item>

                    <ListGroup.Item>
                        <h2>Payment Method</h2>
                        <p><strong>Method: </strong>
                            {order.paymentMethod}</p>
                        {order.isPaid ? <Message varient='success'>Paid on {order.paidAt}</Message>:<Message varient='danger'>Not paid</Message>}
                    </ListGroup.Item>

                    <ListGroup.Item>    
                        <h2>Order Items</h2>
                        {order.orderItems.length === 0 ? <Message>Your order is empty</Message> : (
                            <ListGroup variant='flush'>
                                {order.orderItems.map((item, index) => (
                                    <ListGroup.Item key={index}>
                                        <Row>
                                            <Col md={1} >
                                                <Image src={item.image} alt={item.name} fluid rounded />
                                            </Col>
                                            <Col>
                                                <Link to={`/product/${item.product}`}>{item.name}</Link>
                                            </Col>
                                            <Col md={4}>
                                                {item.qty} x ${item.price} = ${item.qty * item.price}
                                            </Col>
                                        </Row>
                                    </ListGroup.Item>
                                ))}
                            </ListGroup>
                        )}
                    </ListGroup.Item>

                </ListGroup>
            </Col>

            <Col md={4}>
                <Card>
                    <ListGroup variant='flush'>
                        <ListGroup.Item>
                            <h2>Order Summery</h2>
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Row>
                                <Col>Items</Col>
                                <Col>${(Number(order.totalPrice) - Number(order.taxPrice)).toFixed(2)}</Col>
                            </Row>
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <Row>
                                <Col>Shipping</Col>
                                <Col>${order.shippingPrice}</Col>
                            </Row>
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <Row>
                                <Col>Tax</Col>
                                <Col>${order.taxPrice}</Col>
                            </Row>
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <Row>
                                <Col>Total</Col>
                                <Col>${order.totalPrice}</Col>
                            </Row>
                        </ListGroup.Item>
                        {loadingDeliver && <Loader />}
                        {errorDeliver && <Message varient='danger'>{errorDeliver}</Message>}
                        {userInfo.isAdmin === 'true' && order.isPaid && !order.isDelivered &&(
                            <ListGroup.Item>
                                <Button type='button' className='btn btn-block' onClick={deliverHandler}>Mark as delivered</Button>
                            </ListGroup.Item>
                        )}
                    </ListGroup>
                </Card>
            </Col>
        </Row>
    </>
}

export default OrderScreen
