import React,{useEffect, useState} from 'react'
import { Button, Table, Row, Col } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import Message from '../components/Message';
import Loader from '../components/Loader';
import { createProduct, listProducts, productDelete } from './../actions/productActions';
import { PRODUCT_CREATE_RESET } from './../constants/productConstants';


const ProductListScreen = ({history, match}) => {

    const [successMessage, setSuccessMessage] = useState();

    const dispatch = useDispatch();
    const productList = useSelector(state=> state.productList);
    const {loading, error, products} = productList;

    const userLogin = useSelector(state=> state.userLogin);
    const { userInfo } = userLogin;

    const createdProduct = useSelector(state=> state.productCreate);
    const { loading:loadingCreate, success:successCreate, error:errorCreate } = createdProduct;

    const productDeleted = useSelector(state=> state.productDelete);
    const { loading:loadingDelete, success:successDelete, error:errorDelete  } = productDeleted;

    useEffect(()=>{
        dispatch({ type:PRODUCT_CREATE_RESET });

        if(userInfo && userInfo.isAdmin === 'true'){
            dispatch(listProducts())
        }else{
            history.push('/login')
        }   

        if(successCreate){
            history.push('/admin/productlist');
        }

    },[dispatch,history,userInfo,successDelete,successCreate])

    const deleteHandler = (id) =>{
        if(window.confirm('Are your sure?')){
            dispatch(productDelete(id))
        }
    }

    const createProductHandler =()=>{
        dispatch(createProduct())
        setSuccessMessage('Sample Product Successfully Created ! Edit it now!')
    }

    return (
        <div>
            <Row className='align-items-center'>
                <Col>
                    <h1>Users</h1>
                </Col>
                <Col className="text-right">
                    <Button className='my-3' onClick={createProductHandler}>
                        <i className="fas fa-plus"></i> Create Product
                    </Button>
                </Col>
            </Row>
            {loadingDelete && <Loader />}
            {loadingCreate && <Loader />}
            {successMessage && <Message varient='success'>{successMessage}</Message>}
            {errorCreate && <Message varient='danger'>{errorCreate}</Message>}
            
            {errorDelete && <Message varient='danger'>{errorDelete}</Message>}
            {loading ? <Loader />:error ? <Message varient='danger'>{error}</Message>:(
                <Table striped bordered hover responsive className='table-sm'>
                    <thead>
                        <tr>
                            <td>ID</td>
                            <td>NAME</td>
                            <td>PRICE</td>
                            <td>CATEGORY</td>
                            <td>BRAND</td>
                            <td></td>
                        </tr>
                    </thead>
                    <tbody>
                        {products.map(product=>(
                            <tr key={product._id}>
                                <td>{product._id}</td>
                                <td>{product.name}</td>
                                <td> 
                                    ${product.price}
                                </td>
                                <td>
                                    {product.category}
                                </td>
                                <td>
                                    {product.brand}
                                </td>
                                <td>
                                    <LinkContainer to={`/admin/product/${product._id}/edit`}>
                                        <Button variant='light' className='btn-sm'>
                                            <i className='fas fa-edit'></i>
                                        </Button>
                                    </LinkContainer>

                                    <Button variant='danger' className='btn-sm' onClick={()=> deleteHandler(product._id)}><i className='fas fa-trash'></i></Button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </Table>
            )}
            
        </div>
    )
}

export default ProductListScreen;
