import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Form, Button, Row, Col, Container } from 'react-bootstrap';
import { saveShippingAddress } from './../actions/cartActions';
import CheckOutSteps from '../components/CheckOutSteps';

const ShippingScreen = ({ history }) => {

    const dispatch = useDispatch();
    const cart = useSelector(state=> state.cart);
    const { shippingAddress } = cart;

    const [address, setAddress] = useState(shippingAddress.address);
    const [city, setCity] = useState(shippingAddress.city);
    const [postalCode, setPostalCode] = useState(shippingAddress.postalCode);
    const [country, setCountry] = useState(shippingAddress.country);

    const submitHandler = (e) =>{
        e.preventDefault();
        dispatch(saveShippingAddress({address,city,postalCode,country}));

        history.push('/payment');
    }

    return <Container>
        <Row className='justify-content-md-center'>
            <Col xs={12} md={6}>

                <CheckOutSteps step1 step2 />

                <h1>Shipping</h1>
                <Form onSubmit={submitHandler} >
                    <Form.Group controlId='address'>
                        <Form.Label>Name</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="Enter Address" value={address} onChange={e => setAddress(e.target.value)}
                        ></Form.Control>
                    </Form.Group>

                    <Form.Group controlId='city'>
                        <Form.Label>City</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="Enter City" value={city} onChange={e => setCity(e.target.value)}
                        ></Form.Control>
                    </Form.Group>

                    <Form.Group controlId='postalCode'>
                        <Form.Label>Postal Code</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="Enter PostalCode" value={postalCode} onChange={e => setPostalCode(e.target.value)}
                        ></Form.Control>
                    </Form.Group>

                    <Form.Group controlId='country'>
                        <Form.Label>Country</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="Enter Country" value={country} onChange={e => setCountry(e.target.value)}
                        ></Form.Control>
                    </Form.Group>

                    <Button type='submit' variant="primary" className="btn-block">Continue</Button>
                </Form>
            </Col>
        </Row>
    </Container>

}

export default ShippingScreen
